<template>
  <div class="m-container">
    <div class="sider">
      <div class="header">
        <span v-if="acticeIndex === 0">系统首页</span>
        <span v-else>登录首页</span>
      </div>
      <router-link to="/home/system"><div class="item" :class="{'active': acticeIndex === 0}" @click="acticeIndex = 0">系统首页</div></router-link>
      <!-- <router-link to="/home/log"><div class="item" :class="{'active': acticeIndex === 1}" @click="acticeIndex = 1">登录首页</div></router-link> -->
    </div>
    <div  style="overflow: scroll;min-width: 1000px">
      <router-view></router-view>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  data () {
    return {
      height: `${document.documentElement.clientHeight - 70}px`,
      acticeIndex: 0
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-row
  width: 100%
  height: 100%
  background-color: $color-back-gray_1
  .sider
    @extend .flex
    @extend .flex-column
    width: 150px
    min-width: 150px
    background-color: $color-back-gray_0
    border-right: 1px solid $color-back-gray_2
    .header
      @extend .flex-center
      width: 100%
      height: 70px
      color: $color-back-gray_3
      font-size: $size-18
      border-bottom: 1px solid $color-back-gray_2
    .item
      @extend .flex-center
      @extend .pointer
      width: 100%
      height: 50px
      color: $color-back-black_1
      font-size: $size-16
      border-bottom: 1px solid $color-back-gray_2
    .active
      color: $color-back-green_0
</style>



